'use client';

import { Metadata } from 'next';
import { Phone, Mail, MapPin, Clock, MessageCircle } from 'lucide-react';
import { motion } from 'framer-motion';
import AnimatedPageHeader from '@/components/AnimatedPageHeader';
import AnimatedContentSection from '@/components/AnimatedContentSection';

export const metadata: Metadata = {
  title: 'Контакты - Prime Balloons',
  description: 'Контактная информация Prime Balloons. Адрес, телефон, email и время работы.',
};

export default function ContactsPage() {
  // Анимационные варианты
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  return (
    <div className="container mx-auto px-4 py-16">
      <AnimatedPageHeader
        title="Контакты"
        description="Свяжитесь с нами любым удобным способом"
        icon={MessageCircle}
        bgColor="bg-pink-100"
        iconColor="text-pink-500"
      />

      <div className="max-w-6xl mx-auto grid grid-cols-1 md:grid-cols-2 gap-8">
        {/* Контактная информация */}
        <AnimatedContentSection
          delay={0.1}
          animation="fadeLeft"
        >
          <h2 className="text-2xl font-bold mb-6">Наши контакты</h2>
          
          <div className="space-y-6">
            <motion.div 
              className="flex items-start"
              whileHover={{ x: 5 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <div className="bg-pink-100 p-3 rounded-full mr-4">
                <MapPin className="h-6 w-6 text-pink-500" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">Адрес</h3>
                <p className="text-gray-600">г. Москва, ул. Пушкина, д. 10, офис 25</p>
                <p className="text-gray-500 text-sm mt-1">Ежедневно 9:00-21:00</p>
              </div>
            </motion.div>

            <motion.div 
              className="flex items-start"
              whileHover={{ x: 5 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <div className="bg-pink-100 p-3 rounded-full mr-4">
                <Phone className="h-6 w-6 text-pink-500" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">Телефон</h3>
                <a href="tel:+74957734375" className="text-gray-600 hover:text-pink-500 transition-colors">
                  +7 (495) 773-43-75
                </a>
                <p className="text-gray-500 text-sm mt-1">Круглосуточно</p>
              </div>
            </motion.div>

            <motion.div 
              className="flex items-start"
              whileHover={{ x: 5 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <div className="bg-pink-100 p-3 rounded-full mr-4">
                <Mail className="h-6 w-6 text-pink-500" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">Email</h3>
                <a href="mailto:info@primeballoons.ru" className="text-gray-600 hover:text-pink-500 transition-colors">
                  info@primeballoons.ru
                </a>
                <p className="text-gray-500 text-sm mt-1">Мы отвечаем в течение 2 часов</p>
              </div>
            </motion.div>

            <motion.div 
              className="flex items-start"
              whileHover={{ x: 5 }}
              transition={{ type: "spring", stiffness: 400, damping: 10 }}
            >
              <div className="bg-pink-100 p-3 rounded-full mr-4">
                <Clock className="h-6 w-6 text-pink-500" />
              </div>
              <div>
                <h3 className="font-semibold text-gray-900">Часы работы</h3>
                <p className="text-gray-600">Ежедневно с 9:00 до 21:00</p>
                <p className="text-gray-500 text-sm mt-1">Доставка круглосуточно</p>
              </div>
            </motion.div>
          </div>
        </AnimatedContentSection>

        {/* Форма обратной связи */}
        <AnimatedContentSection
          delay={0.3}
          animation="fadeRight"
        >
          <h2 className="text-2xl font-bold mb-6">Напишите нам</h2>
          
          <form className="space-y-4">
            <div>
              <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-1">
                Ваше имя
              </label>
              <motion.input
                whileFocus={{ scale: 1.01 }}
                transition={{ type: "spring", stiffness: 300, damping: 10 }}
                type="text"
                id="name"
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none transition-all"
                placeholder="Иван Иванов"
              />
            </div>
            
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-1">
                Email
              </label>
              <motion.input
                whileFocus={{ scale: 1.01 }}
                transition={{ type: "spring", stiffness: 300, damping: 10 }}
                type="email"
                id="email"
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none transition-all"
                placeholder="example@mail.ru"
              />
            </div>
            
            <div>
              <label htmlFor="phone" className="block text-sm font-medium text-gray-700 mb-1">
                Телефон
              </label>
              <motion.input
                whileFocus={{ scale: 1.01 }}
                transition={{ type: "spring", stiffness: 300, damping: 10 }}
                type="tel"
                id="phone"
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none transition-all"
                placeholder="+7 (___) ___-__-__"
              />
            </div>
            
            <div>
              <label htmlFor="message" className="block text-sm font-medium text-gray-700 mb-1">
                Сообщение
              </label>
              <motion.textarea
                whileFocus={{ scale: 1.01 }}
                transition={{ type: "spring", stiffness: 300, damping: 10 }}
                id="message"
                rows={4}
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-pink-500 focus:border-pink-500 outline-none transition-all resize-none"
                placeholder="Ваше сообщение..."
              ></motion.textarea>
            </div>
            
            <div className="pt-2">
              <motion.button
                whileHover={{ scale: 1.03 }}
                whileTap={{ scale: 0.98 }}
                transition={{ type: "spring", stiffness: 400, damping: 10 }}
                type="submit"
                className="w-full bg-pink-500 text-white py-3 px-6 rounded-lg font-medium hover:bg-pink-600 transition-colors"
              >
                Отправить сообщение
              </motion.button>
            </div>
          </form>
        </AnimatedContentSection>
      </div>

      {/* Карта */}
      <AnimatedContentSection
        delay={0.5}
        animation="scale"
        className="mt-16 bg-transparent p-0 shadow-none"
      >
        <h2 className="text-2xl font-bold mb-6 text-center">Мы на карте</h2>
        <div className="rounded-xl overflow-hidden shadow-lg h-96 relative">
          <iframe 
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2245.6518405076047!2d37.618675815787776!3d55.75628988055625!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46b54a50b315e573%3A0xa886bf5a3d9b2e68!2z0JzQvtGB0LrQvtCy0YHQutC40Lkg0JrRgNC10LzQu9GM!5e0!3m2!1sru!2sru!4v1649155417202!5m2!1sru!2sru" 
            width="100%" 
            height="100%" 
            style={{ border: 0 }} 
            allowFullScreen={true} 
            loading="lazy" 
            referrerPolicy="no-referrer-when-downgrade"
          ></iframe>
        </div>
      </AnimatedContentSection>
    </div>
  );
} 